	module.exports = {"data":{"project":{"html":"<p>When Citibike was announced, I think I was more excited about the data that would be generated and shared publicly than the bikes themselves. This project was a quick experiment built on top of the real-time Citibike stations data.</p>\n<p>I used Google Maps and d3.js to plot out each of the stations on the map:</p>\n<ul>\n<li><span style=\"color: green;\">green</span> stations have > 5 bikes available</li>\n<li><span style=\"color: orange;\">orange</span> stations have between 1 and 5 bikes available</li>\n<li><span style=\"color: red;\">red</span> stations have 0 bikes available</li>\n<li>a <span style=\"color: green;\">green</span> station with a larger circle radius has more available bikes</li>\n</ul>\n<p>To use the tool, simply enter an origin address and a destination address (both NYC addresses), and hit the \"plot\" button. Bicycle directions between the closest stations to each of the two points that you've specified will be plotted on the map.</p>","frontmatter":{"title":"Citibike","link":"http://citibike.amaliaviti.com","tags":null,"github":"http://github.com/aamaliaa/citibike","image":null,"created":"June 2013"}}},"pathContext":{"slug":"/projects/citibike/"}}

